import React, { Component } from 'react';
import domtoimage from 'dom-to-image'; // https://github.com/tsayen/dom-to-image



class Blurrer extends Component {
    render() {
        var node = document.getElementById('root');

        var link = document.createElement('a');
        domtoimage.toJpeg(node, {
            quality: 0.5,
            /*style: {filter: 'blur(15px)'}*/ // better to apply the blur AFTER the image is compressed
        }).then(function (dataUrl) {
            link.download = 'my-image-name.jpeg';
            link.innerHTML = 'descargar'
            link.href = dataUrl;
            document.body.appendChild(link);
            // link.click();

            var img = new Image();
            img.src = dataUrl;
            document.body.appendChild(img);
        }).catch(function (error) {
            console.error('oops, something went wrong!', error);
        });
        return (
            <div></div>
        )
    }
}
export default Blurrer;


